@import url(https://fonts.googleapis.com/css2?family=Henny+Penny&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cabin+Sketch&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body{margin:0;font-family:'Roboto', sans-serif;font-size:1.5em;background-image:url(/memory-game/static/media/background.12a0ba8b.png);background-attachment:fixed;background-position:center;background-repeat:no-repeat;background-size:cover}.flex-wrapper{display:flex;justify-content:space-between;flex-wrap:wrap}.header{max-width:40%;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin-left:2em;margin-top:1em;margin-bottom:0.5em;border-radius:15px;padding:10px;background:rgba(255,255,255,0.1)}.header .title-wrapper{display:flex}.header .title-wrapper .logo{width:135px;height:135px}.header .title-wrapper .title{font-family:'Cabin Sketch', cursive;font-size:2.5em}.scoreboard{font-size:2em;font-family:'Cabin Sketch', cursive;border-radius:15px;padding:10px;background:rgba(255,255,255,0.1);margin-right:3em;margin-top:0.5em;margin-left:1em;text-align:right;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.cards{margin:auto;max-width:1080px;text-align:center;display:flex;flex-wrap:wrap;justify-content:space-evenly}.card{width:231px;height:315px;overflow:hidden;background-color:darkgoldenrod;border-radius:10px;font-size:25px;font-family:'Henny Penny', cursive;margin:10px;border:3px outset goldenrod}.card .card-art{max-width:217px;max-height:260px;border-radius:10px;margin:5px}.card .card-text{position:relative;bottom:15px}.footer{margin:auto;margin-top:8em;max-width:-webkit-fit-content;max-width:-moz-fit-content;max-width:fit-content;font-size:0.8rem;border-radius:15px;padding:10px;background:rgba(255,255,255,0.7)}.footer .about-header{margin-top:0px;margin-bottom:0px}.footer .credits{display:flex;justify-content:space-evenly}a:link{text-decoration:none}

